import { BASE_IMAGE_URL,getConfig } from '@/utils/api'

export default {
	data() {
		return {
			avatarUrl: "",
			nickName: "",
			phone: "17373349812"
		}
	},
	onLoad() {
		this.getConfig();
	},
	onShow() {
		let userInfo = uni.getStorageSync("userInfo");
		if(userInfo){
			this.avatarUrl = userInfo.m_avatar;
			this.nickName = userInfo.m_name;
		}
		
	},
	methods: {
		async getConfig(){
			let res = await getConfig();
			if(res.code == 1000){
				res.data.map((item) => {
					if(item.i_name == "客服电话"){
						this.phone = item.i_info;
					}
				})
			}
		},
		callPhone: function(){
			console.log(this.phone);
			uni.makePhoneCall({
				phoneNumber:this.phone,
				success: () => {
					console.log("成功拨打电话")
				}
			})
		},
		goToAboutUs: function(){
			uni.navigateTo({
				url: '/pages/about_us/about_us'
			});
		},
		goToFix: function(){
			uni.navigateTo({
				url: '/pages/fix_page/fix_page'
			});
		},
		goToRecordList: function(){
			let userInfo = uni.getStorageSync("userInfo");
			if(!userInfo){
				uni.navigateTo({
					url: '/pages/login/login'
				});
				return;
			}
			uni.navigateTo({
				url: '/pages/record_list/record_list'
			});
		},
		
		goToAddress: function(){
			let userInfo = uni.getStorageSync("userInfo");
			if(!userInfo){
				uni.navigateTo({
					url: '/pages/login/login'
				});
				return;
			}
			uni.navigateTo({
				url: '/pages/address_list/address_list?my=1'
			});
		},
		goToInOrderList: function(index){
			let userInfo = uni.getStorageSync("userInfo");
			if(!userInfo){
				uni.navigateTo({
					url: '/pages/login/login'
				});
				return;
			}
			
			if(userInfo.m_role == 0){
				//游客
				uni.showModal({
					title: "提示",
					content: "无权浏览，如需使用请联系客服开通权限",
					showCancel: false,
					success(res) {}
				})
			}else{
				uni.navigateTo({
					url: '/pages/in_order_list/in_order_list?index='+index
				});
			}
		},
		goToOrderList: function(index){
			let userInfo = uni.getStorageSync("userInfo");
			if(!userInfo){
				uni.navigateTo({
					url: '/pages/login/login'
				});
				return;
			}
			
			if(userInfo.m_role == 0){
				//游客
				uni.showModal({
					title: "提示",
					content: "无权浏览，如需使用请联系客服开通权限",
					showCancel: false,
					success(res) {}
				})
			}else{
				uni.navigateTo({
					url: '/pages/order_list/order_list?index='+index
				});
			}
		}
	}
}
